.chats{
    position: relative;
    height: 400px;
    width: 250px;
}

.chats_header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 10px;
    padding-right: 10px;
    background-color: #059ee0;
    height: 50px;
}

.chats_search > input {
    outline: none;
    background-color: transparent;
    border: none;
    font-size: 12px;
    flex: 1;
    color: white;
}

.chats_search > input::placeholder{
color: white;
opacity: 1;
}

.chats_avatar{
    height: 25px !important;
    width: 25px !important;
}

.chats_search{

    display: flex;
    align-items: center;
    flex: 1;
    padding-left: 8px;
}

.chats_posts{
box-shadow: 1px -7px 7px -6px rgba(0,0,0,0.44);
height: 359px;
margin-top: -9px;
border-top-left-radius: 10px;
border-top-right-radius: 10px;
overflow: scroll;
background-color: white;
}

.chats_posts::-webkit-scroolbar{
    display: none;
}

.chats_posts{
    -ms-overflow-style:none;
    scrollbar-width: none;
}

.chats_takePicIcon{
    position: absolute;
    background-color: white;
    border-radius: 200px;
    color: gray;
    font-size: 40px !important;
    cursor: pointer;
    bottom: 0;
    left: 50%;
    transform: translate(-50%,-50%);
}

.chats_takePicIcon:hover{
    opacity: 0.8;
}
.chats_searchIcon{
    color: white;
    font-size: 13px !important;
}

.chats_chatIcon{
    color: white;
    font-size: 18px !important;
}