.preview{
   position: relative;
}
.preview_close{
    position: absolute;
    top: 0;
    margin: 5px;
    cursor: pointer;
    color: white;
}
.preview_toolbarRight{
    color: white;
    position: absolute;
    right:0;
    display: flex;
    flex-direction: column;
    margin: 5px;
}

.preview_toolbarRight > .MuiSvgIcon-root{
    font-size: 24px !important;
    margin-bottom: 10px;
    cursor: pointer;
}
.preview_footer{
    background-color: yellow;
    color:black;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    border-radius: 30px;
    padding: 7px;
    cursor: pointer;position: absolute;
    bottom: 0;
    right: -10%;
    transform: translate(-50%,-50%);
}

.preview_footer > h2{
    font-size:8px;
    margin-right:3px;
}
.preview_sendIcon{
    font-size: 10px !important;
}